import React, { Component } from "react";
import CatchPoint from "./catchPoint";

class Points extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentItemIndex: 0
    };
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  componentDidMount() {
    this.interval = setInterval(() => {
      let { currentItemIndex } = this.state;
      let { catchPoints } = this.props;

      if (currentItemIndex == catchPoints.length - 1) {
        currentItemIndex = 0;
      } else {
        currentItemIndex++;
      }
      this.setState({ currentItemIndex });
    }, 1000);
  }

  render() {
    return (
      <div className="catch-point-container">
        <CatchPoint
          item={this.props.catchPoints[this.state.currentItemIndex]}
        />
      </div>
    );
  }
}

export default Points;
